body {
  font-family: Arial, sans-serif;
  background-color: #344459;
}

.navbar {
  margin-bottom: 20px;
}

.projects .project {
  margin-bottom: 20px;
}

.image-viewer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-viewer .backdrop {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}

.image-viewer .viewer-content {
  position: relative;
  z-index: 10;
  max-width: 90%;
  max-height: 90%;
}

.image-viewer .viewer-content img {
  max-width: 100%;
  max-height: 100%;
  display: block;
  margin-bottom: 10px;
}

.image-viewer .thumbnails {
  display: flex;
  justify-content: center;
}

.image-viewer .thumbnails img {
  width: 50px;
  height: 50px;
  margin: 0 5px;
  cursor: pointer;
}
