/* src/components/AboutMe.css */

.about-me-section {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Esto asegura que la sección ocupe toda la altura de la ventana */
    text-align: center;
    background-color: #344459; /* Color de fondo para la sección */
    padding: 20px;
  }
  
  .about-me-content {
    max-width: 600px; /* Ancho máximo para el contenido */
    background: #8da6f2; /* Fondo blanco para el contenido */
    padding: 20px; /* Espaciado interno */
    border-radius: 5px; /* Bordes redondeados */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra para darle un efecto elevado */
  }
  
  .about-me-content h2 {
    margin-bottom: 20px; /* Espaciado debajo del título */
  }
  
  .about-me-content p {
    font-size: 16px; /* Tamaño de fuente del párrafo */
    line-height: 1.6; /* Altura de línea para mejor legibilidad */
  }
  