/* src/components/Projects.css */

.projects {
    margin-top: 100px; /* Asegura que los proyectos no queden ocultos bajo el navbar */
  }
  
  .project {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .project .main-image {
    width: 100%;
    max-width: 750px;
    cursor: pointer;
    transition: transform 0.7s;
  }
  
  .project .main-image:hover {
    transform: scale(1.05);
  }
  
  .image-viewer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1000;
  }
  
  .image-viewer-content {
    position: relative;
    background: rgba(0, 0, 0, 0);
    padding: 20px;
    border-radius: 10px;
    max-width: 90%;
    max-height: 90%;
  }
  
  .image-viewer-content img {
    max-width: 100%;
    max-height: 80vh;
    display: block;
    margin: 0 auto;
  }
  
  .image-viewer-content .thumbnails {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
  
  .image-viewer-content .thumbnails img {
    width: 50px;
    height: 50px;
    margin: 0 5px;
    cursor: pointer;
    transition: transform 0.2s;
  }
  
  .image-viewer-content .thumbnails img:hover {
    transform: scale(1.1);
  }
  
  .image-viewer-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    cursor: pointer;
  }
  